.navigation {
    position: absolute;
    width: 100%;
    padding: 10px 0px;
    background: linear-gradient(rgba(20, 20, 20, 0.1), rgba(20, 20, 20, 0.1));
  }
  
  .logo img {
    max-width: 90px;
  }
  .primary-nav {
    padding-right: 50px;
  }
  .primary-nav li {
    display: inline-block;
    margin: 0px 20px;
    color: white;
    font-size: 13pt;
  }
  
  .primary-nav a {
    color: white;
    padding-bottom: 5px;
    -webkit-transition: border 50ms linear;
    -moz-transition: border 50ms linear;
    -o-transition: border 50ms linear;
    -ms-transition: border 50ms linear;
    transition: border 50ms linear;
  }
  .primary-nav a:hover {
    text-decoration: none;
    color: white;
    border-bottom: 2px solid #f5f5f5;
  }